  }
}

.animate-border__img-bottom {
  position: relative;
  bottom: -2px;
  left: -2px;
  animation: vertical-bottom 6s linear infinite;
}

.animate-border__img-top {
  position: relative;
  bottom: 2px;
  left: -2px;
  animation: vertical-top 3s linear infinite;
}

@keyframes vertical-top {
  0% {
    top: 0;
  }
  50% {
    top: 55%;
  }
  100% {
    top: 0;
  }
}
@keyframes vertical-bottom {
  0% {
    top: 0;
  }
  50% {
    top: -55%;
  }
  100% {
    top: 0;
  }
}
